// Functional Imports
import { useState, useEffect } from "react";
import cssNameSimplifier from "../../../utility/css-name-simplifier";
import isEmpty from "../../../utility/is-empty";

// Componet Imports
import PopupModel from "../../../componets/PopupModel/PopupModel";
import { TextField, Divider, Tooltip, IconButton, Button, useMediaQuery } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import CloseIcon from '@mui/icons-material/Close';

// Style Imports
import importedStyles from './EditSource.module.css';

const STYLES = cssNameSimplifier(importedStyles);

function EditSource(props){
    const [sourceData, setSourceData]   = useState(null);
    const mediaQueryCheck               = useMediaQuery('(min-width:450px)');
    
    useEffect(() => {
        setSourceData(props.sourceData);
    }, [props.sourceData]);

    async function handelFormSubmission(e){
        e.preventDefault();
        const formTypeData = new FormData(e.currentTarget);

        const jsonTypeData = {};
        formTypeData.forEach((value, key) => {
            jsonTypeData[key] = value;
        });

        props.requstHandler && props.requstHandler({ 
            type: 'FORM_SUBMISSION', 
            action: { 
                data: formTypeData 
            } 
        });
    }

    function handelModelClose(){
        props.requstHandler && props.requstHandler({ type: 'CLOSE_MODAL' });
    }

    function handelCreateNewSubSource(){
        const source = JSON.parse(JSON.stringify(sourceData));
        const subSource = source.subsources || [];

        subSource.unshift({ id: subSource.length + 1 + Math.random(), label: '' });
        source.subsources = subSource;

        setSourceData(source);
    }

    function handelDeleteRow(id){
        const source = JSON.parse(JSON.stringify(sourceData));
        const subSource = source.subsources || [];

        const filteredSubSouurce = subSource.filter(ss => ss.id !== id);
        source.subsources = filteredSubSouurce;

        setSourceData(source);
    }

    return (
        sourceData !== null && 
        <PopupModel title='Source Information' open={true} handelModelClose={handelModelClose}>
            <form 
                className={STYLES.formContainer} 
                onSubmit={handelFormSubmission}
                onKeyDown={e => e => e.key === 'Enter' ? e.preventDefault() : ''}
            >
                <p className={STYLES.sourceTitle}>
                    <span>S</span>ource
                </p>

                <div className={STYLES.flexContainer}>
                    <TextField
                        className={STYLES.input}
                        name="source"
                        defaultValue={sourceData.label}
                        required
                    />
                    <input type="hidden" name="sourceId" value={sourceData.id} />
                </div>

                <Divider sx={{ marginY: 4, marginX: 2}} />

                <div className={STYLES.container}>

                    <p className={STYLES.sourceTitle}>
                        <span>S</span>ub
                        <span> S</span>ource
                    </p>

                    <Tooltip title="Create New">
                        <IconButton sx={{ marginRight: 2 }} onClick={handelCreateNewSubSource}>
                            <AddIcon sx={{ fontSize: 30 }}/>
                        </IconButton>
                    </Tooltip>
                </div>
                
                {
                    !isEmpty(sourceData.subsources) && 
                    <>
                        <div className={STYLES.list}>
                        {
                            sourceData.subsources.map((ss, index) => (
                                <div className={STYLES.subsourceContainer} key={ss.id}>
                                    <div className={STYLES.indexInput}>
                                        <p>{++index}</p>

                                        <TextField
                                            className={STYLES.input}
                                            name={`SUB_SOURCE_${ss.id}`}
                                            defaultValue={ss.label}
                                            required
                                        />
                                    </div>
            
                                    <div className={STYLES.deleteIcon}>
                                        <Tooltip title="Delete">
                                            <IconButton onClick={e => handelDeleteRow(ss.id)}>
                                            {
                                                mediaQueryCheck? <DeleteIcon /> : <CloseIcon />
                                            }
                                            </IconButton>
                                        </Tooltip>
                                    </div>
                                </div>
                            ))
                        }
                        </div>
        
                        <Divider sx={{ marginY: 4, marginX: 2}} />
                    </>
                }

                <div className={STYLES.popupFooter}>
                    <Button variant="outlined" onClick={handelModelClose}>Cancel</Button>
                    <Button variant="contained" type="submit">Save</Button>
                </div>
            </form>
        </PopupModel>
    )
}

export default EditSource;