// Functional Imports
import cssNameSimplifier from "../../utility/css-name-simplifier";

// Component Imports
import { Box, Modal, IconButton, useMediaQuery, Typography, Divider } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";

// Styled Imports
import importedStyles from './PopupModel.module.css';

const STYLES = cssNameSimplifier(importedStyles);

const boxStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '70vw',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    maxHeight: '85vh',
    p: 4,
};


function PopupModel(props){
    boxStyle.width  = useMediaQuery('(min-width:700px)')? '70vw' : '95vw';

    return (
        <Modal open={props.open}>
            <Box sx={boxStyle} >
                <IconButton className={STYLES.closeIcon} onClick={props?.handelModelClose}>
                    <CloseIcon  />
                </IconButton>

                {
                    props.title && (
                        <>
                            <Typography variant="h5" component="h2" textAlign="center">
                                <strong>
                                    {props.title}
                                </strong>
                            </Typography>
            
                            <Divider variant='middle' sx={{ marginY: 2 }} />
                        </>
                    )
                }

                <div className={STYLES.childrenContainer}>
                    <div style={{ marginRight: 10 }}>
                        {props.children}
                    </div>
                </div>
            </Box>
        </Modal>

    )
}

export default PopupModel;